@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
body,html {
  margin: 0;
  font-family: 'Noto Sans', sans-serif, Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input, textarea, button{
  outline: none;
  border: none;
  font-family: 'Noto Sans', sans-serif, Arial, Helvetica, sans-serif;
}

:root{
  --primary-bg: rgb(5, 12, 7);
  --secondary-bg: rgb(0, 20, 0);
  --primary-bg1: rgb(4, 9, 83);
  --secondary-bg1: rgb(9, 50, 97);
  --tertiary-bg : rgb(177, 177, 177);
  --primary-clr: rgb(221,221,221);
}

.primarybg{
  background-color: rgb(4, 9, 83);
  background-color: var(--primary-bg1);
}

.secondarybg{
  background-color: rgb(9, 50, 97);
  background-color: var(--secondary-bg1);
}

.tertiarybg{
  background-color: rgb(177, 177, 177);
  background-color: var(--tertiary-bg);
}

.primaryclr{
  color:rgb(221,221,221);
  color:var(--primary-clr)
}
.app_container{
  height: 100vh;
}

.container-flex-center{
  display: flex;
  justify-content: center;
  align-items: center;
}

.header-container{
    box-shadow: 0px 10px 44px 4px rgba(1, 79, 54, 0.2);
}

.header_logo{
   padding: 12px 0px;
   font-size: 2em;
   text-align: center;
}
.suggestionbox{
    width: 40px;
    height: 40px;
    position: absolute;
    border-radius: 4px;
    transition: 0.2s;
}
.textfield_container{
    margin: 100px auto 0px auto;
    width: 600px;
    max-width: 90vw;
    height: 300px;
    position: relative;
}
.textfield_container > textarea{
    background: rgba(0, 0, 0, 0.4);
    resize: none;
    border: 2px solid rgb(0, 56, 56);
    color: rgb(189, 206, 206);
    letter-spacing: 1px;
    font-size: 18px;
    width: 100%;
    height: 100%;
}
